// Définition d'un nouveau composant appelé `button-counter`
//import axios from "axios";
import Customs from "./customService.js";
import swal from "sweetalert";

export default {
    name: 'SocietyAdd',
    data: () => ({
        userId: "",
        newSociety: {
            name: "",
            description: "",
            creatorId: ""
        },
        jobs: [{text:'Dirigeant', value: 0}, {text:'Employé', value: 1}],
    }),

    methods: {
        async addSociety() {
            try {
                let user = Customs.getUserDetails();
                this.userId = user._id;
                this.newSociety.creatorId = user._id
                await this.$http
                    .post("/society/add", {
                        society: this.newSociety,
                    })
                    .then(response => {
                        swal("Success", response.data.message, "success");
                        this.$emit('custom-event-name', {isAdd: true});
                        return (response);
                    });
                this.newSociety.name = '';
                this.newSociety.description = '';
            }
            catch (err) {
                let error = err.response;
                if (error.status == 409) {
                swal("Error", error.data.message, "error");
                } else {
                swal("Error", error.data.err.message, "error");
                }
            }
        },
    },

    created() {
        let user = Customs.getUserDetails();
        this.userId = user._id;
        this.newSociety.creatorId = user.id;
    },

    updated() {
    }
};