import VueJwtDecode from "vue-jwt-decode";

export default {
    name: 'SocietyFilter',

    data: () => ({
        userId: "",
        listSocieties: [],
        search: "",
        currSize: 0
    }),

    methods: {
        getUserDetails() {
            let token = localStorage.getItem("jwt");
            if (token == null) {
                this.$router.push("/");
            }
            let decoded = VueJwtDecode.decode(token);
            this.userId = decoded._id;
        },
        searchSocieties() {
            this.getUserDetails();
            this.$http
            .post("/society/byName/", {
                name: this.search
            })
            .then(response => {
                this.listSocieties = response.data.societies;
            });
            return null;
        },
        async refreshed() {
            
        }
    },

    created() {
        this.getUserDetails();
        this.refreshed();
    },

    updated() {
        if (this.search.length >= 3 && this.currSize != this.search.length) {
            this.currSize = this.search.length;
            this.searchSocieties();
        }
    }
};