<template>
<div>
  <navbar></navbar>
  <div class="society">
    <societyAdd @custom-event-name="societyAdded" />
    <societyFilter />
    <societyPersonal ref="societyPersonal"/>
  </div>
  </div>
</template>
<script>
import navbar from "@/components/Navbar";
import societyPersonal from "@/components/SocietyPersonal";
import societyAdd from "@/components/SocietyAdd";
import societyFilter from "@/components/SocietyFilter";

export default {
  components: {
    societyPersonal,
    societyAdd,
    societyFilter,
    navbar
  },
  methods: {
    societyAdded(event) {
      if (event.isAdd) {
        this.refreshed();
      }
    },

    refreshed() {
      this.$refs.societyPersonal.getMySocieties();
    }
  }
};
</script>